*{
      margin: 0;
      padding: 0;
}
html, body, #root, .App {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.mainpage_header_title{
  width: 100%;
  height: 8vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainpage_header_category{
  height: 100%;
  width: 100%;
}
.mainpage_header_category nav {
  height: 5vh;
  border: 1px solid black;
  width: 100%;
}

.mainpage_header_category ul {
  justify-content: space-around;
  align-items: center;
  border: 1px solid black;
  height: 100%;
  display: flex;
  list-style: none;
  padding: 0;
  width: 100%; /* ul 요소의 너비를 100%로 설정 */
  
}

.mainpage_header_category li {
  margin-right: 10px;
  
}
.mainpage_header_category li:hover {
  font-size: 30px;
  
}
